@import url(https://fonts.googleapis.com/css?family=Nunito);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.canvas{padding-left:0;padding-right:0;margin-left:auto;margin-right:auto;display:block;width:500px}.guide{font-family:Bradley Hand, cursive;text-align:center;vertical-align:middle;line-height:50px}body{background-color:#8EC5FC;background-image:linear-gradient(90deg, #8EC5FC 0%, #E0C3FC 100%)}.portfolio{text-decoration:none;font-family:Bradley Hand, cursive;font-size:1.5em;float:right;padding-right:10%;color:black}.portfolio span{font-weight:100;transition:all .5s}.portfolio span:nth-child(2){transition-delay:.1s}.portfolio span:nth-child(3){transition-delay:.2s}.portfolio span:nth-child(4){transition-delay:.3s}.portfolio:hover span{font-weight:700}.portfolio:hover span:nth-child(5){transition-delay:.3s}.portfolio:hover span:nth-child(4){transition-delay:.3s}.portfolio:hover span:nth-child(3){transition-delay:.2s}.portfolio:hover span:nth-child(2){transition-delay:.1s}.portfolio:hover span:nth-child(1){transition-delay:0s}.portfolio.hover-shadow:hover{text-shadow:1px 1px 0px #ddd,2px 2px 0px #ddd,3px 3px 0px #ddd}.portfolio.hover-color:hover{border-color:darkmagenta}

:root{--atlas: linear-gradient(to right, #feac5e, #c779d0, #4bc0c8);--solidvault: linear-gradient(to right, #3a7bd5, #3a6073);--limeade: linear-gradient(to right, #a1ffce, #faffd1);--dusk: linear-gradient(to right, #2c3e50, #fd746c)}html{height:100%;overflow:hidden}body{margin:0;padding:0;-webkit-perspective:1px;perspective:1px;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;height:100%;overflow-y:scroll;overflow-x:hidden;font-family:Nunito}h1{font-size:250%}p{font-size:140%;line-height:150%;color:#333}.slide{position:relative;padding:25vh 10%;min-height:100vh;width:100vw;box-sizing:border-box;box-shadow:0 -1px 10px rgba(0,0,0,0.7);-webkit-transform-style:inherit;transform-style:inherit}#projectpic{position:absolute;top:50%;width:320px;height:240px;-webkit-transform:translateZ(0.25px) scale(0.75) translateX(-94%) translateY(-100%);transform:translateZ(0.25px) scale(0.75) translateX(-94%) translateY(-100%);padding:10px;border-radius:5px;background:rgba(240,230,220,0.7);box-shadow:0 0 8px rgba(0,0,0,0.7)}#projectpic:nth-child(2n+1){left:35%}#projectpic:nth-child(2n){left:75%}.slide:before{content:"";position:relative;top:0;bottom:0;left:0;right:0;box-shadow:0 0 8px 1px rgba(0,0,0,0.7)}.title{width:50%;padding:5%;border-radius:5px;background:rgba(240,230,220,0.7);box-shadow:0 0 8px rgba(0,0,0,0.7)}.slide:nth-child(2n) .title{margin-left:0;margin-right:auto}.slide:nth-child(2n+1) .title{margin-left:auto;margin-right:0}.slide,.slide:before{background:50% 50% / cover}.header{text-align:center;font-size:175%;color:#fff;text-shadow:0 2px 2px #000}#title{background:linear-gradient(to right, #feac5e, #c779d0, #4bc0c8);background:var(--atlas);z-index:2}#title h1{-webkit-transform:translateZ(0.05px) scale(0.75);transform:translateZ(0.05px) scale(0.75);-webkit-transform-origin:50% 0%;transform-origin:50% 0%}#slide1{background:linear-gradient(to right, #3a7bd5, #3a6073);background:var(--solidvault);z-index:2}#slide2{background:linear-gradient(to right, #2c3e50, #fd746c);background:var(--dusk);z-index:2}#slide3{background:linear-gradient(to right, #a1ffce, #faffd1);background:var(--limeade);z-index:2}#slide4{background:#222}.center{position:absolute;top:50%;left:0;right:0;bottom:0;margin:auto}.table{width:50%;height:11%;background-color:#d4e5ff}.table .monitor-wrapper{top:0;background:#050321;width:95%;height:87%;box-shadow:0px 2px 2px 2px rgba(0,0,0,0.3)}.table .monitor-wrapper .monitor{top:0;width:95%;height:80%;background-color:#344151;overflow:hidden;white-space:nowrap;box-shadow:inset 0px 5px 10px 2px rgba(0,0,0,0.3)}.table .monitor-wrapper .monitor .text1{top:0;font-size:1.8em;position:relative;display:inline-block;-webkit-animation:move 15s infinite linear;animation:move 15s infinite linear;color:#EBB55F;margin:0;padding:0}@-webkit-keyframes move{from{left:800px}to{left:-2800px}}@keyframes move{from{left:800px}to{left:-2800px}}#profilepic{position:relative;width:20%;height:20%}#connectpic{position:relative;width:10%;height:10%;margin:3%}

